.Notifications {
    border: 1px dashed #e1354b;
    padding: 1rem;
    position: relative;
    margin: 1rem;
    font-family:serif;
  }
  
  .close-icon {
    position: absolute;
    top: 10px;
    right: 10px;
    border: none;
    background-color: transparent;
  }
  
  ul li[data-priority='default'] {
    color: blue;
  }
  
  ul li[data-priority='urgent'] {
    color: red;
  }