body {
  font-family: sans-serif;
}

.App-logo {
  width: 200px;
  height: 200px;
}

.App-header {
  background-color: white;
  border-bottom: 3px solid #e1354b;
  height: 200px;
}

.App-header h1 {
  display: inline;
  position: relative;
  top: -6rem;
  color: #e1354b;
}

.App-body {
  background-color: white;
  padding: 4rem;
}

.App-footer {
  background-color: white;
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0px;
  border-top: 3px solid #e1354b;
  font-style:italic;
  
  padding: 1rem 0;
}
.App-footer{
  font-family:serif;
  font-size:18px;
}
